.ca-seo-structure {
  height: 260px;
  overflow: hidden;
  position: relative;
  width: 100%;
  background: #f2f2f2;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
.ca-seo-structure .ca-seo-slide {
  height: 100%;
  display: table;
  padding: 70px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  width: 100%;
}
.ca-seo-structure .ca-seo-slide p {
  width: 70%;
  font-size: 26px;
  color: rgb(116, 116, 116);
  display: table-cell;
  vertical-align: middle;
  padding: 0 50px;
}
.ca-seo-structure .ca-seo-slide .ca-seo-slide-img {
  width: 30%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.ca-seo-structure .ca-seo-slide-nav {
  padding: 20px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  width: 100%;
  z-index: 0;
}
.ca-seo-structure .slide-prev {
  cursor: pointer;
  float: left;
}
.ca-seo-structure .slide-prev::after {
  border-top: 2px solid #888888;
  border-left: 2px solid #888888;
  border-radius: 1px;
  content: "";
  display: block;
  height: 20px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 20px;
}
.ca-seo-structure .slide-next {
  cursor: pointer;
  float: right;
}
.ca-seo-structure .slide-next::before {
  border-top: 2px solid #888888;
  border-right: 2px solid #888888;
  border-radius: 1px;
  content: "";
  display: block;
  height: 20px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 20px;
}
.ca-seo-structure .ca-seo-slide-dots {
  padding: 20px;
  position: absolute;
  bottom: 0%;
  z-index: 1;
  text-align: center;
  width: 100%;
}
.ca-seo-structure .slide-dot {
  border: 2px solid #919191;
  border-radius: 50%;
  cursor: pointer;
  display: inline-block;
  height: 10px;
  width: 10px;
}



